import {useSpring, animated} from 'react-spring'
import * as easings from 'd3-ease'

const Menu = ({fontSize, navOpen, nextPage, setItemType, redirect}) => {

    const LinkAnimate = (index) => useSpring({
        from: {
            marginLeft: -10,
            opacity: 0
        },
        to: {
            marginLeft: !navOpen?0:-10,
            opacity: !navOpen?0:1
        },
        delay: 1200 + (index* 150),
        config: {
            duration: 450,
            easing: easings.easeCubic
        }
    })

    
    const setNextPage = (pageName) => {
        nextPage(pageName)
        setItemType(pageName)
    }


    return (
        <animated.div className={`menu ${navOpen?'menu-active':''}`} style={Menu}>
            <div className="menu-links">
                {/* <animated.h1 style={LinkAnimate(1)} onClick={() => setNextPage("Works")}>Work</animated.h1> */}
                <animated.h1 style={LinkAnimate(2)} onClick={() => setNextPage("Projects")}>Projects</animated.h1>
                <animated.h1 style={LinkAnimate(3)} onClick={() => redirect("https://github.com/prateekbose")}>GitHub</animated.h1>
                <animated.h1 style={LinkAnimate(4)} onClick={() => redirect("https://www.linkedin.com/in/prateek-bose-790a4316b/")}>LinkedIn</animated.h1>
                {/* <animated.h1 style={LinkAnimate(3)}>Behance</animated.h1> */}
            </div>
            <div className="menu-buttons">
                <button onClick={() => redirect("https://www.linkedin.com/in/prateek-bose-790a4316b/")}>Message</button>
                <button onClick={() => redirect("mailto:prateekbose20011@gmail.com?body=Hi!")}>Mail</button>
            </div>
        </animated.div>
    )
}

export default Menu